import React from 'react';

class Message extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <li className={'sent'}>
                <img src={'http://emilcarlsson.se/assets/mikeross.png'} alt="" />
                <p>{this.props.Text}</p>
            </li>
        )
    }
}

export default Message;